.App {
  text-align: center;
  color: white;
  background-color: #282c34;
  width: 100vw;
  height: 100vh;
  padding: 20px;
}

input,button{
    border: none;
    padding: 5px;
    border-radius: 8px;
    width: calc(60% - 20px);
    box-sizing: border-box;
    margin-right: 20px;
    margin-top: 10px;
}

@media only screen and (max-width: 400px) {
  .App-header {
    flex-direction:column;
  }
}

.coll{
  display: flex;
  flex-direction:column;
  width: 80%;
  justify-content:center;
  align-items:center;

}
.App-header {
  background-color: #282c34;
  display: flex;
}



